.fc {
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
    justify-content: space-between;
}

.fi {
    width: 40%;
    margin: 10px;
}

ul{
    padding: 20px;
    /* background-color: #000; */
}
li {
    /* color:#ccc; */
    font-size: 24px;
}

.fakeimg {
    margin: 10px;
    background-color: #eaeaea;
    width: 100%;
    border-radius: 5px;
    /* overflow: hidden; */
}

.logo2 {
    width: 110%;
    height: 110%;
    mix-blend-mode: multiply;
    position: relative;
    top: -2%;
    left: -5%;
}

.news1 {
    column-count: 2;
    column-gap: 50px;
    column-rule-style: outset;
    column-rule-width: 1px;
    text-align: justify;
}

.news2 {
    column-count: 3;
    column-gap: 30px;
}

.row {  
    display: -ms-flexbox; 
    display: flex;
    -ms-flex-wrap: wrap; 
    flex-wrap: wrap;
    /* justify-content: space-between; */
}

.side {
    width: 10%;
}

.main {
    flex-direction: row-reverse;
    float: right;
    width: 85%;
    /* padding: 20px; */
}

.flex-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
    justify-content: space-between;
    width: 400px;
    height: 250px;
    background-color: lightgrey;
}

.br {
    width: 100px;
    height: 100px;
}

.a1 {
    position: fixed;
    bottom: 3%;
    left: 50%;
    width: 50px;
    transition:transform 0.5s, filter 0.5s;
}

.a1:hover {
    cursor:pointer;
    filter: invert(100%);
    transform: scale(1.3);
}

.a2 {
    position: fixed;
    bottom: 10%;
    right: 1%;
    width: 40px;
    transition:transform 0.5s;
}

.a2:hover {
    cursor:pointer;
    transform: scale(1.5);
}

.ok{
    border-radius: 25px;
    transition:transform 0.5s, box-shadow 0.5s;
}

.ok:hover {
    cursor:pointer;
    /* transform: scale(1.05); */
    transform: translate(-15px,-15px);
    box-shadow: 10px 10px 10px #888888;
}